<template>
  <div class="inner__circle">
    <vue-circle
      ref="myprogress"
      :progress="innerProgress"
      :size="120"
      :reverse="false"
      line-cap="round"
      :fill="fill"
      empty-fill="rgba(0, 0, 0, .1)"
      :animation-start-value="0.0"
      :start-angle="55"
      insert-mode="append"
      :thickness="7"
      :show-percent="false">

      <h2>{{getStudyDashboard.certification_level}}</h2>
    </vue-circle>
  </div>
</template>

<script>
  import VueCircle from 'vue2-circle-progress'
  export default {
    components: {
      VueCircle
    },
    data(){
      return{
        fill : { gradient: ["#4C61FC", "#49C5FE"] }
      }
    },
    computed: {
      getStudyDashboard () {
        const data = this.$store.state.stateStudyDashboard
        if (data == null) {
          return 0
        } else {
          return data.data.data
        }
      },
      innerProgress () {
        const data = this.getStudyDashboard
        const todayPoint = data.study.points_until_today
        const passPoint = data.study.points_to_pass
        const returnPoint = todayPoint / passPoint * 100
        const resultPoint = (isNaN(returnPoint) ? 0 : returnPoint)

        if (data == null) {
          return 0
        } else {
          return resultPoint
        }
      }
    }
  }
</script>
<style lang="scss">

</style>



