<template>
  <div class="progress__goal--circle">
    <vue-circle
      ref="myprogress"
      :progress="progressGoal"
      :size="160"
      :reverse="false"
      line-cap="round"
      :fill="fill"
      empty-fill="rgba(0, 0, 0, .1)"
      :animation-start-value="0.0"
      :start-angle="55"
      insert-mode="append"
      :thickness="9"
      :show-percent="false"
      @vue-circle-progress="progress"
      @vue-circle-end="progress_end">
    </vue-circle>
    <svg>
      <path stroke-linecap="round" ref="arc2" fill="none" stroke="rgba(255,255,255,.7)" stroke-width="9" />
    </svg>
    <div class="points__togo" v-if="!expectedPointCondition">
      <div class="points__togo-data">
        <h1>{{this.pointsToGo}}</h1>
        <!-- <h1>999999</h1> -->
        <h4>Points to go</h4>
      </div>
    </div>
    <div class="progress__thumb__icon" v-if="expectedPointCondition">
      <ThumbupIcon
        width="40px"
        height="40px"
        color="#fafafa"
      />
    </div>
  </div>
</template>

<script>
  import VueCircle from 'vue2-circle-progress'
  import ThumbupIcon from '../../../components/icons/ThumbupIcon'

  export default {
    components: {
      VueCircle,
      ThumbupIcon
    },
    props: [
      'expectedPointCondition'
    ],
    data(){
      return{
        fill : { gradient: ["#FECA49", "#EE6D3D"] }
      }
    },
    mounted () {
      let titikVal = this.getStudyDashboard.percentage_days; //GOAL VALUE
      const newtitikVal = titikVal/100;

      if (titikVal >= 100) {
      	titikVal = 100;		//To prevent the dial to overlap
      }

      function polarToCartesian(centerX, centerY, radius, angleInDegrees) {
        const angleInRadians = (angleInDegrees + 90);
        const dailyGoal = (angleInRadians + 360 / 100 * titikVal) * Math.PI / 180.0;
        return {
          x: centerX + (radius * Math.cos(dailyGoal)),
          y: centerY + (radius * Math.sin(dailyGoal))
        };
      }

      function describeArc(x, y, radius, startAngle, endAngle){
        const start = polarToCartesian(x, y, radius, endAngle);
        const end = polarToCartesian(x, y, radius, startAngle);
        const arcSweep = endAngle - startAngle <= 180 ? "0" : "1";
        const d = [
          "M", start.x, start.y,
          "A", radius, radius, 0, arcSweep, 0, end.x, end.y
        ].join(" ");
        return d;
      }

      this.$refs.arc2.setAttribute("d", describeArc(80, 80, 75.5, 180, 180));
    },
    computed: {
      getStudyDashboard () {
        let data = this.$store.state.stateStudyDashboard
        if (data == null) {
          return 0
        } else {
          return data.data.data
        }
      },
      progressGoal () {
        let data = this.getStudyDashboard.percentage_points

        if (data == null) {
          return 0
        } else {
          return data
        }
      },
      titikValCondition () {
        let titikVal = this.getStudyDashboard.percentage_days;
        function getConstantValue() {
          if (titikVal >= 100) {
            return 100
          } else {
            return titikVal
          }
        }
        const titikValResult = getConstantValue()
        let stepVal = this.getStudyDashboard.percentage_points

        if (titikValResult >= stepVal) {
          return "#4C61FC"
        } else {
          return "#fafafa"
        }
      },
      pointsToGo () {
        let expectedPoints = this.getStudyDashboard.total_points_expected_today
        let totalPoints = this.getStudyDashboard.total_points_until_today

        return expectedPoints - totalPoints
      }
    },
    methods:{
      progress(event,progress,stepValue){
        // console.log(stepValue);
        // if (stepValue > 30) {
        //   this.$refs.myprogress.updateFill('#e3f218');
        // }
      },
      progress_end(event){
        console.log("Circle progress end");
      }
    }
  }
</script>
<style lang="scss">

</style>



