<template lang="html">
  <div class="progress__goal">
    <h3>Progress To Goal</h3>
    <ProgressCircle :expectedPointCondition="expectedPointCondition" />
    <h4 v-if="!expectedPointCondition">Keep up the good work!</h4>
    <h4 v-if="expectedPointCondition">Congratulation!</h4>
  </div>
</template>

<script>
import ProgressCircle from './ProgressCircle'

export default {
  components: {
    ProgressCircle
  },
  computed: {
    getStudyDashboard () {
      const data = this.$store.state.stateStudyDashboard
      if (data == null) {
        return 0
      } else {
        return data.data.data
      }
    },
    expectedPointCondition () {
      const todaysPoint = this.getStudyDashboard.total_points_until_today
      const expectedPoint = this.getStudyDashboard.total_points_expected_today

      if (todaysPoint >= expectedPoint) {
        return true
      } else {
        return false
      }
    }
  }
}
</script>

<style lang="css">
</style>



