<template lang="html">
  <div class="barchart__box">
    <h3>Comprehension</h3>
    <div class="graph b2">
      <div class="graph__bar__cont">
        <span class="graph__legend trn" data-trn-key="now">now</span>
        <label>{{comprehensionValue[0] / 12.5 * 8}}</label>
        <div class="bar__container">
          <div class="graph__bar__each-bg"></div>
          <div class="graph__bar__each" data-value="80" :style="'width' + comprehensionValue[0] / 12.5 * 8 + '%'"></div>
        </div>
  		</div>

  		<div class="graph__bar__cont">
        <span class="graph__legend">w 1</span>
        <label>{{comprehensionValue[1] / 12.5 * 8}}</label>
        <div class="bar__container">
          <div class="graph__bar__each-bg"></div>
          <div class="graph__bar__each" data-value="80" :style="'width' + comprehensionValue[1] / 12.5 * 8 + '%'"></div>
        </div>
  		</div>

  		<div class="graph__bar__cont">
        <span class="graph__legend">w 2</span>
        <label>{{comprehensionValue[2] / 12.5 * 8}}</label>
        <div class="bar__container">
          <div class="graph__bar__each-bg"></div>
          <div class="graph__bar__each" data-value="80" :style="'width' + comprehensionValue[2] / 12.5 * 8 + '%'"></div>
        </div>
  		</div>

  		<div class="graph__bar__cont">
        <span class="graph__legend">w 3</span>
        <label>{{comprehensionValue[3] / 12.5 * 8}}</label>
        <div class="bar__container">
          <div class="graph__bar__each-bg"></div>
          <div class="graph__bar__each" data-value="80" :style="'width' + comprehensionValue[3] / 12.5 * 8 + '%'"></div>
        </div>
  		</div>

  		<div class="v__bar">
    		<div class="v__line"></div>
    		<div class="v__line"></div>
    		<div class="v__line"></div>
    		<div class="v__line"></div>
    		<div class="v__line"></div>
  		</div>
    </div>
  </div>
</template>

<script>
export default {
  components: {
  },
  props: [
    'getWeeklyProgress'
  ],
  data () {
    return {

    }
  },
  computed: {
    comprehensionValue () {
      const weeklyProgress = this.getWeeklyProgress.data.data
      const resultData = weeklyProgress.map(data => data.comprehension_grammar);

      return resultData
    }
  }
}
</script>

<style lang="scss">
</style>



