<template>
  <div class="outter__circle">
    <vue-circle
      ref="myprogress"
      :progress="outterProgress"
      :size="136"
      :reverse="false"
      line-cap="round"
      :fill="fill"
      empty-fill="rgba(0, 0, 0, .1)"
      :animation-start-value="0.0"
      :start-angle="55"
      insert-mode="append"
      :thickness="7"
      :show-percent="false">
    </vue-circle>
  </div>
</template>

<script>
  import VueCircle from 'vue2-circle-progress'
  export default {
    components: {
      VueCircle
    },
    data(){
      return{
        fill : { gradient: ["#FECA49", "#EE6D3D"] }
      }
    },
    methods:{
    },
    computed: {
      outterProgress () {
        const data = this.$store.state.stateStuDashCoaching
        const todayPoint = data.points_until_today
        const passPoint = data.points_to_pass
        const returnPoint = todayPoint / passPoint * 100
        const resultPoint = (isNaN(returnPoint) ? 0 : returnPoint)

        if (data == null) {
          return 0
        } else {
          return resultPoint
        }
      }
    }
  }
</script>
<style lang="scss">

</style>



