<template lang="html">
  <div class="cert__completion">
    <CertCompletionCircle />
    <div class="points__to__goal">
      <span>{{getStudyDashboard.total_points_until_today}} / {{getStudyDashboard.total_points_to_pass}}</span>
    </div>
    <div class="coach__study__container">
      <div class="studying__status">
        <ul>
          <li v-for="(data, index) in studyMasteryTest" :class="data.status"></li>
        </ul>

        <div class="studying__point">
          <h4>{{getStudyDashboard.study.points_until_today}}</h4>
          <span>Study</span>
        </div>
      </div>

      <div class="coaching__status">
        <ul>
          <li v-for="(data, index) in coachSessions" :class="data.status"></li>
        </ul>

        <div class="coaching__point">
          <h4>{{getStuDashCoaching.points_until_today}}</h4>
          <span>Coach</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import CertCompletionCircle from './CertCompletionCircle'

export default {
  components: {
    CertCompletionCircle
  },
  computed: {
    getStudyDashboard () {
      const data = this.$store.state.stateStudyDashboard
      if (data == null) {
        return 0
      } else {
        return data.data.data
      }
    },
    getStuDashCoaching () {
      const data = this.$store.state.stateStuDashCoaching
      if (data == null) {
        return 0
      } else {
        return data.data
      }
    },
    coachSessions () {
      return this.getStuDashCoaching.sessions
    },
    studyMasteryTest () {
      return this.getStudyDashboard.study.mastery_tests
    }
  }
}
</script>

<style lang="scss">
</style>



