<template>
    <div id="app">
      <div @click="onoff"  class="BxLottieDekstop">
         <lottie :options="defaultOptions" v-on:animCreated="handleAnimation" class="BxLottieDekstop__LottieArea"/>
      </div>
       
        <div style="visibility:hidden">
          <input type="range" value="0.5" min="0" max="3" step="0.5"
                   v-on:change="onSpeedChange" v-model="animationSpeed">
        </div>
        <!-- <button v-on:click="stop">stop</button>
        <button v-on:click="pause">pause</button>
        <button v-on:click="play">play</button> -->
       
    </div>
</template>


<script>
import Lottie from './Lottie';
import * as animationData from '../../staticjson/AstonotLayangan.json';

  export default {
    name: 'app',
    components: {
      'lottie': Lottie
    },
    data() {
      return {
        defaultOptions: {animationData: animationData},
        animationSpeed: 0.5,
        btnPlay : true
      }
    },

    methods: {
      handleAnimation: function (anim) {
        this.anim = anim;
        console.log(this.anim)
      },

      stop: function () {
        this.anim.stop();
      },

      play: function () {
        this.anim.play();
      },

      pause: function () {
        this.anim.pause();
      },

      onSpeedChange: function () {
        this.anim.setSpeed(this.animationSpeed);
        
      },

      onoff(){
        if (this.btnPlay == true){
          this.pause()
          this.btnPlay = false
        }else{
          this.btnPlay = true
           this.play()
        }
        console.log(this.anim)
      }

    },
    mounted() {
      this.onSpeedChange()
      this.btnPlay = true
    },
  }
</script>



