<template lang="html">
  <div class="barchart__box">
    <h3>Pronunciation</h3>
    <div class="graph b2">
      <div class="graph__bar__cont">
        <span class="graph__legend trn" data-trn-key="now">now</span>
        <label>{{pronunctiationValue[0] / 12.5 * 8}}</label>
        <div class="bar__container">
          <div class="graph__bar__each-bg"></div>
          <div class="graph__bar__each" data-value="80" :style="'width' + pronunctiationValue[0] / 12.5 * 8 + '%'"></div>
        </div>
  		</div>

  		<div class="graph__bar__cont">
        <span class="graph__legend">w 1</span>
        <label>{{pronunctiationValue[1] / 12.5 * 8}}</label>
        <div class="bar__container">
          <div class="graph__bar__each-bg"></div>
          <div class="graph__bar__each" data-value="80" :style="'width' + pronunctiationValue[1] / 12.5 * 8 + '%'"></div>
        </div>
  		</div>

  		<div class="graph__bar__cont">
        <span class="graph__legend">w 2</span>
        <label>{{pronunctiationValue[2] / 12.5 * 8}}</label>
        <div class="bar__container">
          <div class="graph__bar__each-bg"></div>
          <div class="graph__bar__each" data-value="80" :style="'width' + pronunctiationValue[2] / 12.5 * 8 + '%'"></div>
        </div>
  		</div>

  		<div class="graph__bar__cont">
        <span class="graph__legend">w 3</span>
        <label>{{pronunctiationValue[3] / 12.5 * 8}}</label>
        <div class="bar__container">
          <div class="graph__bar__each-bg"></div>
          <div class="graph__bar__each" data-value="80" :style="'width' + pronunctiationValue[3] / 12.5 * 8 + '%'"></div>
        </div>
  		</div>

  		<div class="v__bar">
    		<div class="v__line"></div>
    		<div class="v__line"></div>
    		<div class="v__line"></div>
    		<div class="v__line"></div>
    		<div class="v__line"></div>
  		</div>
    </div>
  </div>
</template>

<script>
export default {
  components: {
  },
  props: [
    'getWeeklyProgress'
  ],
  data () {
    return {

    }
  },
  computed: {
    pronunctiationValue () {
      const weeklyProgress = this.getWeeklyProgress.data.data
      const resultData = weeklyProgress.map(data => data.pronunciation);

      return resultData
    }
  }
}
</script>

<style lang="scss">
</style>



